<template>
  <div>
    <div class="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pb-2 mb-3 border-bottom">
      <h1 class="h2 text-capitalize">
        {{ `${this.userTypeCode} Management` }}
      </h1>
      <div class="btn-toolbar mb-2 mb-md-0">
        <div class="btn-group mr-2" v-if="userId">
          <router-link class="btn btn-sm btn-outline-secondary" :to="{ name: 'users' }">Back</router-link>
        </div>
        <div class="btn-group mr-2" v-if="!userId">
          <router-link class="btn btn-sm btn-outline-secondary" :to="{ name: 'users', params: { id: 'new' }}">Add User</router-link>
        </div>
      </div>
    </div>
    <template v-if="userTypeCode == 'client'">
      <clientGrid v-if="!userId" :userTypeId="userTypeId" />
      <clientDetails v-if="userId" :userTypeId="userTypeId" :userId="userId" />
    </template>
    <template v-if="userTypeCode == 'studio'">
      <studioGrid v-if="!userId" :userTypeId="userTypeId" />
      <studioDetails v-if="userId" :userTypeId="userTypeId" :userId="userId" />
    </template>
    <template v-if="userTypeCode == 'service'">
      <serviceGrid v-if="!userId" :userTypeId="userTypeId" />
      <serviceDetails v-if="userId" :userTypeId="userTypeId" :userId="userId" />
    </template>
    <template v-if="userTypeCode == 'backoffice'">
      <backofficeGrid v-if="!userId" :userTypeId="userTypeId" />
      <backofficeDetails v-if="userId" :userTypeId="userTypeId" :userId="userId" />
    </template>
  </div>
</template>

<script>
export default {
  components: {
    clientGrid: () => import("./components/client-grid"),
    clientDetails: () => import("./components/client-details"),
    studioGrid: () => import("./components/studio-grid"),
    studioDetails: () => import("./components/studio-details"),
    serviceGrid: () => import("./components/service-grid"),
    serviceDetails: () => import("./components/service-details"),
    backofficeGrid: () => import("./components/backoffice-grid"),
    backofficeDetails: () => import("./components/backoffice-details"),
  },
  computed: {
    userTypeId() {
      return this.$store.getters.userTypes.filter(
        (x) => x.code == this.userTypeCode
      )[0].userTypeId;
    },
    userTypeCode() {
      return this.$route.params.userTypeCode;
    },
    userId() {
      return this.$route.params.id;
    },
  },
};
</script>
